@import url("https://fonts.googleapis.com/css2?family=Syne:wght@400;500;700&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 1920px) {
  html {
    font-size: 14px;
  }
}

/* BASE */
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Syne", sans-serif;
}

#app {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
}

a {
  color: initial;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

div[role="button"] {
  cursor: pointer;
}

/* SHARED */
.container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

.call-to-action {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.call-to-action span {
  font-size: 1.25rem;
  text-decoration: underline;
  text-underline-offset: 4px;
}

.call-to-action:hover span {
  color: #4700fc;
}

.call-to-action img:last-of-type {
  display: none;
}

.call-to-action:hover img:first-of-type {
  display: none;
}

.call-to-action:hover img:last-of-type {
  display: block;
  transform: translateX(0.25rem);
}

.section-subtitle {
  color: #4700fc;
  font-size: 1.125rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.section-title {
  font-size: 4rem;
  line-height: 58px;
  margin-bottom: 3rem;
}

.page-header {
  min-height: 22.5rem;
  background: radial-gradient(
    circle at top left,
    #4700fc 1%,
    #e3b7ab 25%,
    #fff7eb 50%,
    #fff
  );
  display: flex;
  align-items: center;
}

.page-header-wrapper {
  display: flex;
  align-content: center;
  margin-top: 4rem;
}

.page-header-wrapper h1 {
  font-size: 4rem;
}

.page-header-wrapper h2 {
  font-size: 2rem;
  font-weight: normal;
  margin-top: 4rem;
}

/* HEADER */
header {
  height: 100px;
}

.header-wrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .nav {
  display: flex;
  gap: 2rem;
}

header .nav .nav-item a {
  font-size: 1rem;
  font-weight: bold;
  position: relative;
}

header .nav .nav-item a:hover {
  color: #4700fc;
}

header .nav .nav-item a:hover::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #4700fc;
}

.menu {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  display: none;
}

/* HOME */
.who-we-are {
  font-size: 2rem;
  line-height: 38px;
  width: 55%;
  padding: 10rem 0;
  position: relative;
}

#logo-blur {
  position: absolute;
  top: 6rem;
  left: 95%;
  width: 643px;
  height: 756px;
  z-index: -1;
}

.you-are-a {
  width: 50%;
  margin-bottom: 10rem;
}

.you-are-a h2 {
  font-weight: bold;
  font-size: 4rem;
}

.you-are-a div {
  margin-top: 2rem;
}

.you-are-a div h6 {
  font-weight: bold;
  font-size: 1.25rem;
}

.you-are-a div p {
  font-size: 1rem;
}

.you-are-a .discover-more {
  margin-top: 2.5rem;
  width: max-content;
}

.service-packages {
  margin-bottom: 5rem;
}

.packages {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, minmax(540px, auto));
  gap: 2rem;
}

.package-card {
  border: 1px solid #000;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

.package-card h4 {
  font-size: 2rem;
}

.package-card h5 {
  font-size: 1.25rem;
}

.package-card .description {
  margin-top: 1.5rem;
  font-size: 1.125rem;
}

.package-card ul {
  margin-top: 1.5rem;
  margin-left: 1rem;
  list-style-type: disc;
}

.package-card .call-to-action {
  margin-top: auto;
  align-self: flex-end;
}

.what-we-do {
  background: linear-gradient(to right, #ccc, #fff);
  padding: 2rem 0 3rem;
}

.what-we-do > div {
  display: flex;
}

.what-we-do .text {
  flex: 1;
}

.what-we-do .text p {
  font-size: 1.25rem;
}

.what-we-do .accordions {
  flex: 2;
  margin-left: 2rem;
}

.accordion {
  padding: 1rem 0;
}

.accordion-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #000;
}

.accordion-title {
  font-size: 2.5rem;
}

.accordion-text {
  margin-top: 1rem;
  font-size: 1rem;
  margin-bottom: 5rem;
}

.solutions {
  padding: 2rem 0;
}

.solutions-wrapper {
  display: flex;
  align-items: center;
}

.solutions-wrapper .text,
.solutions-wrapper .image {
  flex: 1;
}

.solutions-wrapper .text {
  padding-right: 2rem;
}

.solutions-wrapper .text .section-title {
  margin-bottom: 2rem;
}

.solutions-wrapper .text .description {
  margin-bottom: 2rem;
  font-size: 1.25rem;
  font-weight: bolder;
}

/* ABOUT */
.paragraph-first {
  padding: 10rem 0;
  font-size: 2rem;
}

.paragraph-second {
  padding: 10rem 0;
  font-size: 2rem;
}

.people {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 2rem;
}

.many-more {
  display: flex;
  justify-content: center;
  align-items: center;
}

.many-more span {
  font-size: 2rem;
  font-weight: bold;
  color: #4700fc;
}

/* CUSTOMERS */
.customers {
  position: relative;
}

.customers > img {
  position: absolute;
  z-index: -1;
}

.customers #texture-2 {
  left: 0;
  top: 20%;
}

.customers #texture-1 {
  right: 0;
  top: 40%;
}

.customers #texture-3 {
  left: 0;
  top: 70%;
}

.customers-wrapper {
  padding: 5rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 5rem;
}

.customers-wrapper section h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.customers-wrapper section p {
  font-size: 1.25rem;
}

.customers-wrapper section > p {
  margin-top: 2rem;
}

.customers-wrapper section ul {
  margin-top: 2rem;
}

.customers-wrapper section ul li {
  padding: 1rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}

.item-text h6 {
  font-size: 1.25rem;
  font-weight: bolder;
}

.item-text h6:last-child {
  margin-top: 1rem;
  text-decoration: underline;
}

.item-text p {
  font-size: 1rem;
}

.learn-more {
  margin: 1rem 0 2rem;
  font-size: 1.7rem;
  font-weight: bold;
  line-height: 38px;
}

.mailto {
  color: #4700fc;
  text-decoration: underline;
}

/* PRODUCTS */
.products {
  position: relative;
  overflow: hidden;
}

.products > img {
  position: absolute;
  z-index: -1;
}

.products #texture-2 {
  left: -5%;
  top: 20%;
}

.products #texture-1 {
  right: 0;
  top: 52.5%;
}

.products #texture-3 {
  left: 0;
  top: 75%;
}

.products-wrapper {
  padding: 5rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 8rem;
}

.product {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.product .image {
  position: relative;
}

.product .image.otis:hover::before {
  content: "";
  position: absolute;
  left: 4px;
  right: 3px;
  top: 4px;
  bottom: 8px;
  border: 2px solid #4700fc;
}

.product .image.prosys,
.product .image.healthbotics,
.product .image.ampup,
.product .image.culturalfit {
  cursor: auto;
}

.product .text {
  padding-right: 2rem;
}

.product .text h4 {
  font-size: 1.5rem;
}

.product .text h2 {
  font-size: 4rem;
  font-weight: normal;
  line-height: 60px;
  letter-spacing: -3%;
  margin-top: 1rem;
}

.product .text p {
  margin-top: 2rem;
  font-size: 1.25rem;
  font-weight: lighter;
}

/* OTIS APPS */
.otis-details {
  position: relative;
  padding: 4rem 0 10rem;
}

.otis-details > img {
  position: absolute;
  z-index: -1;
}

.otis-details #texture-2 {
  left: -5%;
  top: 25%;
}

.otis-details #texture-1 {
  right: 0;
  top: 50%;
}

.otis-details #texture-3 {
  left: 0;
  top: 90%;
}

.mcast-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 6rem 0;
}

.mcast-info .text h4 {
  font-size: 1.5rem;
}

.mcast-info .text h4:first-child {
  margin-bottom: 2rem;
}

.otis-title {
  font-size: 2rem;
  line-height: 25px;
  margin-top: 4rem;
}

.otis-paragraph {
  font-size: 1.25rem;
  font-weight: lighter;
  line-height: 25px;
  margin-top: 3rem;
}

.otis-apps {
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.otis-apps li {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.otis-apps .app-description {
  font-size: 1.25rem;
  line-height: 25px;
}

.otis-apps .app-name {
  font-weight: bold;
}

.otis-examples {
  margin-top: 4rem;
  padding: 3rem 0;
  background: #e3e3e3;
}

.otis-examples .otis-example {
  display: none;
}

.otis-examples .images-wrapper {
  display: flex;
  justify-content: space-between;
}

/* CONTACT */
.contact {
  padding: 10rem 0;
  min-height: calc(100vh - 100px - 315px);
  background: linear-gradient(to right, #4700fc, #d9d9d9);
  color: white;
}

.contact-wrapper {
  display: flex;
}

.contact-wrapper > div {
  flex: 1;
}

.contact-wrapper h1 {
  font-size: 4rem;
}

.contact-wrapper h5 {
  margin-top: 1rem;
  font-size: 1.25rem;
  font-weight: lighter;
  max-width: 400px;
}

.contact-details {
  margin-top: 1rem;
}

.contact-details .location {
  font-size: 1.125rem;
}
.contact-details-mobile .location span,
.contact-details .location span {
  font-weight: bold;
}

.contact-details .location {
  padding-top: 2rem;
}

.contact-details-mobile .location:last-child {
  margin-top: 2rem;
}

.contact-details-mobile .phone,
.contact-details .phone {
  margin-top: 1.5rem;
}

.form-wrapper {
  display: flex;
}

.form-wrapper p {
  align-self: center;
  width: 100%;
  text-align: center;
  font-size: 2rem;
}

.contact-form {
  width: 360px;
}

.contact-form > div {
  height: 4rem;
  position: relative;
}

.contact-form .error-message {
  position: absolute;
  left: 1rem;
  bottom: 0.25rem;
  font-size: 0.625rem;
  color: #ff2828;
}

.contact-form input,
.contact-form textarea {
  font-family: "Syne", sans-serif;
  font-size: 1rem;
  outline: none;
}

.contact-form input {
  height: 100%;
  width: 100%;
  padding: 0 1rem;
}

.contact-form textarea {
  width: 100%;
  padding: 1rem;
  height: 8rem;
  vertical-align: top;
  resize: none;
}

.send-message {
  width: 100%;
  height: 4rem;
  padding: 0 1rem;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #4700fc;
  font-family: "Syne", sans-serif;
  font-size: 1.25rem;
  font-weight: bold;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.send-message:hover {
  background-color: #6c33fd;
}

.arrow-black {
  display: none;
}

.contact-details-mobile {
  display: none;
}

/* GET IN TOUCH */
.get-in-touch {
  padding: 4rem 0 5rem;
  background: linear-gradient(to right, #4700fc, #d9d9d9);
  color: #fff;
}

.get-in-touch h3 {
  font-size: 1.125rem;
  text-transform: uppercase;
}

.get-in-touch .action {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-top: 1rem;
}

.get-in-touch .action a img:last-of-type {
  display: none;
}

.get-in-touch .action a:hover img:first-of-type {
  display: none;
}

.get-in-touch .action a:hover img:last-of-type {
  display: inline-block;
}

.get-in-touch h2 {
  font-size: 4rem;
}

/* FOOTER */
footer {
  padding: 4rem 0 5rem;
  background-color: #1e1e1e;
  color: #fff;
}

.footer-wrapper {
  display: flex;
  gap: 10rem;
}

.footer-wrapper .nav {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.footer-wrapper .nav li a {
  font-family: "Syne", sans-serif;
  color: #fff;
  font-size: 1.25rem;
  font-weight: bold;
}

.footer-wrapper .address,
.footer-wrapper .rights {
  font-size: 1.125rem;
}

.footer-wrapper .address {
  margin-top: 2.5rem;
}

.footer-wrapper .rights {
  margin-top: 2rem;
}

.footer-wrapper .socials a {
  font-size: 1.25rem;
  font-weight: bold;
  text-decoration: none;
  color: #fff;
}

.back-to-top {
  outline: none;
  border: none;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
  display: none;
}

.back-to-top span {
  color: #fff;
  font-family: "Syne", sans-serif;
  font-size: 18px;
  font-weight: bold;
  text-decoration: underline;
  text-underline-offset: 4px;
}

/* ERROR */
.error-page {
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.error-page h1 {
  font-size: 4rem;
}

.error-page p {
  margin-top: 1rem;
  font-size: 1.5rem;
  color: #aaa;
}

/* CAROUSEL */
.slide-content {
  height: 780px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

#hero-1 {
  background-image: url("/public/images/hero/hero-m.png");
}
#hero-2 {
  background-image: url("/public/images/hero/hero-3.png");
}
#hero-3 {
  background-image: url("/public/images/hero/hero-vim.png");
}

.slide-content-wrapper {
  height: 100%;
}

.slide-content-wrapper .container {
  height: 100%;
  display: flex;
  align-items: center;
}

.slide-content-wrapper h1 {
  font-size: 6rem;
  width: 75%;
}

.slide-content-wrapper p {
  font-size: 1.25rem;
  font-weight: lighter;
  color: #616161;
  margin-top: 1rem;
  width: 60%;
}

/* SIDEBAR */
.sidebar {
  position: absolute;
  inset: 0;
  background-color: #fff;
  z-index: 10;
  transform: translateX(100%);
  transition: all 0.2s;
  display: flex;
  align-items: center;
}

.open {
  transform: translateX(0);
}

#app.sidebar-open {
  overflow-y: hidden;
}

.close-button {
  outline: none;
  border: none;
  background: transparent;
  position: absolute;
  top: 2.4rem;
  right: 1.25rem;
  cursor: pointer;
}

.sidebar ul {
  width: 100%;
}

.sidebar ul li {
  border-bottom: 2px solid #000;
  text-align: center;
  padding: 1.5rem 0;
}

.sidebar ul li a {
  font-size: 1.5rem;
  position: relative;
}

.sidebar ul li a:hover {
  color: #4700fc;
}

.sidebar ul li a:hover::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #4700fc;
}

/* SWIPER */
.swiper-button-prev,
.swiper-button-next {
  width: 3.25rem;
  height: 3rem;
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
}

.swiper-button-prev {
  background-image: url(/public/images/prev.png);
  left: 2rem;
}

.swiper-button-next {
  background-image: url(/public/images/next.png);
  right: 2rem;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1360px) {
  #logo-blur {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .slide-content-wrapper h1 {
    font-size: 5rem;
    width: 80%;
  }
  .slide-content-wrapper p {
    font-size: 1.125rem;
  }
}

@media screen and (max-width: 960px) {
  .slide-content-wrapper h1 {
    font-size: 4rem;
  }
  .slide-content-wrapper p {
    font-size: 1rem;
  }
  .product {
    flex-direction: column-reverse;
  }
  .product .image {
    width: 100%;
  }
  .product .image img {
    width: 100%;
  }
  .mcast-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 2rem;
    margin: 2rem 0;
  }
}

@media screen and (max-width: 900px) {
  .packages {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, minmax(540px, auto));
    gap: 2rem;
  }
}

@media screen and (max-width: 800px) {
  .contact {
    padding: 4rem 0;
  }
  .contact-wrapper {
    flex-direction: column;
    gap: 3rem;
  }
  .contact-details {
    display: none;
  }
  .contact-details-mobile {
    display: block;
  }
  .form-wrapper p {
    text-align: left;
  }
}

@media screen and (max-width: 768px) {
  .menu {
    display: block;
  }
  header .nav {
    display: none;
  }
  .slide-content {
    height: 490px;
  }
  .slide-content-wrapper h1 {
    width: 80%;
  }
  .slide-content-wrapper p {
    margin-top: 0.5rem;
    width: 90%;
  }
  .what-we-do {
    background: linear-gradient(to bottom, #ccc, #fff);
  }
  .what-we-do .section-title {
    margin-bottom: 1rem;
  }
  .what-we-do > div {
    flex-direction: column;
  }
  .what-we-do .accordions {
    margin-left: 0;
    margin-top: 2rem;
  }
  .solutions {
    margin-bottom: 4rem;
  }
  .solutions-wrapper {
    flex-direction: column-reverse;
    gap: 2rem;
  }
  .footer-wrapper {
    gap: 5rem;
  }
}

@media screen and (max-width: 600px) {
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
  }
  .who-we-are,
  .you-are-a {
    width: 100%;
  }
  .who-we-are {
    padding: 5rem 0;
  }
  .packages {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, minmax(540px, auto));
    gap: 2rem;
  }
  .get-in-touch {
    padding: 2rem 0 3rem;
  }
  .get-in-touch .action {
    flex-direction: column;
    align-items: flex-start;
  }
  .about-us-image {
    margin-left: -1rem;
    margin-right: -1rem;
  }
  .paragraph-first {
    padding: 5rem 0 3rem;
  }
  .paragraph-second {
    padding: 5rem 0;
  }
  .page-header-wrapper {
    margin-bottom: 4rem;
  }
  .learn-more {
    margin: 0;
  }
  .otis-apps {
    gap: 3rem;
  }
  .otis-apps li {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  .otis-details {
    padding-bottom: 6rem;
  }
  .send-message {
    background-color: #ff9800;
    color: #000;
  }
  .arrow-white {
    display: none;
  }
  .arrow-black {
    display: inline;
  }
  .otis-examples {
    padding: 0;
  }
  .otis-examples .otis-example {
    display: block;
  }
  .otis-examples .images-wrapper {
    display: none;
  }
  .contact {
    min-height: calc(100vh - 100px - 152px);
  }
  footer {
    padding: 2rem 0;
  }
  .footer-wrapper {
    justify-content: space-between;
  }
  .footer-wrapper .address,
  .footer-wrapper .nav,
  .footer-wrapper .socials {
    display: none;
  }
  .back-to-top {
    display: flex;
  }
}

@media screen and (max-width: 500px) {
  .people {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 390px) {
  .contact-form {
    width: 100%;
  }
}
